@import url(https://fonts.googleapis.com/css2?family=Fugaz+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
font-family: "Fugaz One" , cursive;
font-size: 18px;
margin: 0;
padding: 0;

} 

body {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color:#262834;
}

h1 {
    color: white;
    font-size: 30px;
    color: red;
    text-decoration: underline;
}

.container {
    top: 50%;
    left: 50%;
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    width: 20%;
    border: 10px solid black;
    background: black;
    border-radius: 5px;
}

.keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(80px, auto);

}

input {
    width: 100%;
    padding-bottom: 5%;
    margin-bottom: 5%;
    border: 1px;
    text-align: right;
    font-size: 50px;
    font-weight: 500;
    letter-spacing: 1px;
}

#clear {
    grid-column:1/3;
    grid-row: 1;
    font-size: 40px;
}

#result {
    grid-column: 3/5;
    grid-row: 5;
    background-color: #FF4365;
}

#backspace {
    font-size: 40px;
}

button {
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background:#262834;
    color: white;
    font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

.highlight {
    background-color: #FF4365;
}
